.top-arrow-container {
  position: fixed;
  right: 0;
  bottom: 100px;
  width: 40px;
  height: 40px;
  margin: 0;
  border: 2px solid #000;
  background: #fff;
  border-right: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.top-arrow-container:hover {
  cursor: pointer;
}
.top-arrow-container:hover a img {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1)
}

.top-arrow-container a {
}

.top-arrow-container a img {
  padding-left: 6px;
  padding-top: 5px;
  width: 30px;
  height: 30px;
  display: block;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}




